//下拉选择品牌页面
import config from '@/api/config.js';
import LetterList from '@/pages/common/letterList.vue';
import {request} from '@/api/reques.js'
let _this;
export default{
	data(){
		return{
			hot:[],
			cars:[],
			letter:'',
			off:false,
			series:[],
			model:{},
			mark:0,
		}
	},
	filters:{
		filterImg(value){
			if(value){
				return config.domain + value
			}
		}
	},
	components:{
		LetterList,
	},
	created() {
		this.getBrand()
		_this = this
	},
	onLoad(option) {
		this.mark = parseInt(option.mark)
	},
	watch:{
		"$store.state.letter":(v)=>{
			_this.letter = 'letter'+v;
		}
	},
	methods:{
		// 获取品牌列表
		getBrand(){
			request('/index/getBrandsList',{},"POST")
			.then((res)=>{
				this.hot = res.data.data.is_hot;
				this.cars = res.data.data;
				//console.log(this.hot)
			})
		},
		// 获取车系
		handleCategory(id,value){
			this.model = value;
			request('/index/getBrandsList',{
				brands_id:id
			},"POST")
			.then((res)=>{
				this.series = res.data.data
				this.off = !this.off
			})
		},
		handleCars(item){
			let carType = {
				brandId:this.model.category_id,
				brandName:this.model.category_name,
				categoryId:item.category_id,
				categoryName:item.category_name,
			}
			if(this.mark == 2){
				this.$store.commit("changeKcCars",carType);
			}else{
				this.$store.commit("changeCars",carType);
			}
			
			this.off = false
			uni.navigateBack({
			    delta: 1
			});
			
		},
		scroll(){
			this.$store.state.letter = ""
		},
		touchMove(){
			console.log(1)
		}
			
		
	}
	
}
